<template>
	<!-- 创建任务 -->
	<view class="create-body">
		<view style="padding-top: 22rpx;">
			<uni-list>
				<uni-list-item>
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						任务名称
					</view>
					<view slot="footer">
						<input placeholder="请输入" name="input" :value="form.name" @input="inputName" />
						<view class="error-style" v-if="errMsg.name != ''">{{ errMsg.name }}</view>
					</view>
				</uni-list-item>
				<uni-list-item>
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						任务类型
					</view>
					<view slot="footer">
						<picker :value="form.type" @change="changeTaskType" :range="taskTypeOptions" range-key="name">
							<text class="cuIcon-right right-icon"></text>
							<input placeholder="请选择" name="input" :value="form.type" readonly style="float:right" />
						</picker>
						<view class="error-style" v-if="errMsg.type != ''">{{ errMsg.type }}</view>
					</view>
				</uni-list-item>
				<uni-list-item>
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						巡检内容
					</view>
					<view slot="footer">
						<textarea @input="inputContent" :value="form.content" placeholder="请输入" style="width:400rpx"></textarea>
						<view class="error-style" v-if="errMsg.content != ''">{{ errMsg.content }}</view>
					</view>
				</uni-list-item>
				<!-- 大队创建任务特定字段 start-->
				<uni-list-item v-if="roleId=='4'">
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						责任中队
					</view>
					<view slot="footer">
						<picker :value="form.midGroup" @change="changeMidGroup" :range="midGroupOptions" range-key="name">
							<text class="cuIcon-right right-icon"></text>
							<input placeholder="请选择" name="input" :value="form.midGroup" readonly style="float:right" />
						</picker>
						<view class="error-style" v-if="errMsg.midGroup != ''">{{ errMsg.midGroup }}</view>
					</view>
				</uni-list-item>
				<!-- 大队创建任务特定字段 end-->
				<!-- 中队创建任务特定字段 start-->
				<template v-if="roleId=='5'">
					<uni-list-item>
						<view slot="header" class="form-title">
							<text class="required-s">*</text>
							巡检片区
						</view>
						<view slot="footer">
							<text class="cuIcon-right right-icon"></text>
							<input placeholder="请选择" name="input" :value="form.area" disabled style="float:right" @click="showMap" />
							<view class="error-style" v-if="errMsg.area != ''">{{ errMsg.area }}</view>
						</view>
					</uni-list-item>
					<uni-list-item>
						<view slot="header" class="form-title">
							<text class="required-s">*</text>
							网格警员
						</view>
						<view slot="footer">
							<text class="cuIcon-right right-icon"></text>
							<input placeholder="请选择" name="input" :value="form.police" disabled style="float:right" @click="choosePolices" />
							<view class="error-style" v-if="errMsg.police != ''">{{ errMsg.police }}</view>
						</view>
					</uni-list-item>
				</template>
				<!-- 中队创建任务特定字段 end-->
				<uni-list-item>
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						预计竣工时间
					</view>
					<view slot="footer">
						<picker mode="date" :value="form.preCompleteTime" @change="changeTime">
							<text class="cuIcon-right right-icon"></text>
							<input placeholder="请选择" name="input" :value="form.preCompleteTime" readonly style="float:right" />
						</picker>
						<view class="error-style" v-if="errMsg.preCompleteTime != ''">{{ errMsg.preCompleteTime }}</view>
					</view>
				</uni-list-item>
			</uni-list>
		</view>
		<view class="flex flex-direction" style="padding:30rpx"><button class="cu-btn bg-blue lg" @click="dispatch">派 发</button></view>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			roleId: uni.getStorageSync('roleId'),
			form: {
				name: '',
				content: '',
				type: '',
				typeCode: '',
				midGroupCode: '',
				midGroup: '',
				preCompleteTime: '',
				area: '',
				areaCode: '',
				police: '',
				policeCode: '',
			},
			midGroupOptions: [],
			taskTypeOptions: [],
			errMsg: {
				name: '',
				content: '',
				type: '',
				midGroup: '',
				preCompleteTime: '',
				area:'',
				police:''
			}
		};
	},
	onLoad() {
		this.getTaskTypeOptions()
		if(this.roleId=='4'){
			this.getMidGroupCorpList();
		}else if(this.roleId=='5'){
			this.getMidGroupArea();
		}
	},
	onShow(){
		let area = uni.getStorageSync('checkedArea');
		let policeCode = uni.getStorageSync('checkedPoliceId');
		let police = uni.getStorageSync('checkedPoliceName');
		if (area) {
			this.form.area = area;
			this.errMsg.area = '';
		}
		if (police) {
			this.form.policeCode = policeCode;
			this.form.police = police;
			this.errMsg.police = '';
		}
	},
	methods: {
		getTaskTypeOptions(){
			//获取任务类型
			taskAPI.getSysDict({type:'task_type'}).then(res=>{
				if(res.code==200){
					this.taskTypeOptions=res.data.map(item=>{
						return{
							name:item.label,
							value:item.value
						}
					})
				}
			})
		},
		getMidGroupArea() {
			//查询中队下所有片区
			taskAPI.getMidGroupArea().then(res => {
				this.areaOptions = res.data.map(item => {
					return {
						name: item.areaName,
						value: item.id
					};
				});
			});
		},
		getMidGroupCorpList() {
			//查询所有中队列表
			taskAPI.getMidGroupCorpList().then(res => {
				if (res.code == 200) {
					this.midGroupOptions = res.data.map(item => {
						return {
							name: item.deptName,
							value: item.deptId
						};
					});
				}
			});
		},
		changeTaskType(event) {
			this.form.typeCode = this.taskTypeOptions[event.detail.value].value;
			this.form.type = this.taskTypeOptions[event.detail.value].name;
			this.errMsg.type = '';
		},
		changeMidGroup(event) {
			this.form.midGroupCode = this.midGroupOptions[event.detail.value].value;
			this.form.midGroup = this.midGroupOptions[event.detail.value].name;
			this.errMsg.midGroup = '';
		},
		changeTime(event) {
			this.form.preCompleteTime = event.detail.value;
			this.errMsg.preCompleteTime = '';
		},
		inputName(event) {
			this.form.name = event.detail.value;
			this.errMsg.name = '';
		},
		inputContent(event) {
			this.form.content = event.detail.value;
			this.errMsg.content = '';
		},
		showMap() {
			//选择片区
			uni.navigateTo({
				url: '../patrolTask/map'
			});
		},
		choosePolices() {
			//选择网格警员
			if (this.form.area == '') {
				uni.showToast({
					icon: 'none',
					title: '请先选择片区'
				});
				return;
			}
			uni.navigateTo({
				url: '../patrolTask/choosePolice'
			});
		},
		checkForm() {
			let flag = true;
			if (this.form.name == '') {
				this.errMsg.name = '请填写任务名称';
				flag = false;
			}
			if (this.form.type == '') {
				this.errMsg.type = '请填写任务类型';
				flag = false;
			}
			if (this.form.content == '') {
				this.errMsg.content = '请填写巡检内容';
				flag = false;
			}
			if (this.form.midGroup == ''&&this.roleId=='4') {
				this.errMsg.midGroup = '请选择责任中队';
				flag = false;
			}
			if (this.form.area == ''&&this.roleId=='5') {
				this.errMsg.area = '请选择巡检片区';
				flag = false;
			}
			if (this.form.police == ''&&this.roleId=='5') {
				this.errMsg.police = '请选择网格警员';
				flag = false;
			}
			if (this.form.preCompleteTime == '') {
				this.errMsg.preCompleteTime = '请选择预计完成事件';
				flag = false;
			}
			return flag;
		},
		dispatch() {
			let flag = this.checkForm();
			if (flag) {
				uni.showModal({
					content: '是否确认派发巡检任务单',
					success: rs => {
						console.log(rs);
						if (rs.confirm) {
							//点击了确定按钮
							let param = {};
							if (this.roleId == '4') {
								//大队创建巡检任务
								param = {
									taskName: this.form.name,
									taskType: this.form.typeCode,
									content: this.form.content,
									jurisdiction: this.form.midGroupCode,
									preCompleteDate: this.form.preCompleteTime
								}
							}else if (this.roleId == '5') {
								//中队创建巡检任务
								param = {
									taskName: this.form.name,
									taskType: this.form.typeCode,
									content: this.form.content,
									preCompleteDate: this.form.preCompleteTime,
									checkArea: this.form.area,
									gridPolice: this.form.policeCode
								}
							}
							taskAPI.createTask(param).then(res => {
								if (res.code == 200) {
									uni.showToast({
										icon: 'none',
										title: '派发成功',
										success: () => {
											uni.navigateTo({
												url: '../patrolTask/index'
											});
										}
									});
								}
							});
						}
					}
				});
			}
		}
	}
};
</script>

<style scoped>
input,
textarea {
	text-align: right;
}
</style>
